import React, { useState } from 'react';
import "./Calc.css";
import { create, all } from 'mathjs';

const math = create(all, {
  number: 'BigNumber',
  precision: 32,
});

math.config({
  order: 'all',
});
const Calc = () => {
 // 134-.865441
 // 134+0.+9
 const [expression, setExpression] = useState("0");
 const [result, setResult] = useState("");
  //operator pops up after decimal
  const clear = () => {
    setExpression("0");
    setResult("");
  };
  /*const numberInput = (event) => {
    const num = event.target.innerText;
    let displayValue = expression.toString();
    const lastChar = displayValue.slice(-1);

    if (displayValue === '0' && num === '0') {
      return;
    }
    else if (lastChar === "0" && num !== "0") {
      displayValue = displayValue.slice(0, -1) + num;
    } else {
      displayValue += num;
    } 
    setExpression(displayValue);
  }*/

  const numberInput = (event) => {
    const num = event.target.innerText;
    let displayValue = expression.toString();
    const lastChar = displayValue.slice(-1);

    if (displayValue === '0' && num === '0') {
      return;
    } else if (lastChar === "0" && num !== "0" && displayValue.length === 1) {
      displayValue = num;
    } else {
      displayValue += num;
    }
    setExpression(displayValue);
}
  const decimal = (event) => {
    const decimal = event.target.innerText;
    const regex = /[-+/*]$/;
    const lastNum = expression.split(/[-+/*]/).pop();
    if (lastNum.indexOf(".") === -1 && !regex.test(expression)) {
      setExpression(expression + decimal);
    }
  }

  /*const addOperator = (event) => {
    const operator = event.target.innerText;
    const regex = /[-+/*]$/;
    if (expression === "" && result !== "") {
      setExpression(result + operator);
    } else if (expression !== "" && regex.test(expression)) {
      if (operator === "-") {
        const lastTwoChars = expression.slice(-2);
        if (lastTwoChars === "--") {
          return; // Prevent more than two consecutive - signs
        }
      }
      if (operator === "-") {
        setExpression(expression + operator);
      } else if (expression.slice(-2) === "*-" || expression.slice(-2) === "+-" || expression.slice(-2) === "/-") {
        setExpression(expression.slice(0, -2) + operator);
      } else {
        setExpression(expression.slice(0, -1) + operator);
      }
    } else {
      setExpression(expression + operator);
    }
  };*/

  const addOperator = (event) => {
    const operator = event.target.innerText;
    const regex = /[-+/*]$/;
    if (expression === "" && result !== "") {
      setExpression(result + operator);
    } else if (expression !== "" && regex.test(expression)) {
      if (operator === "-") {
        const lastTwoChars = expression.slice(-2);
        if (lastTwoChars === "--") {
          return; // Prevent more than two consecutive - signs
        }
      }
      if (operator === "-") {
        setExpression(expression + operator);
      } else if (expression.slice(-2) === "*-" || expression.slice(-2) === "+-" || expression.slice(-2) === "/-") {
        setExpression(expression.slice(0, -2) + operator);
      } else {
        setExpression(expression.slice(0, -1) + operator);
      }
    } else {
      const lastChar = expression.slice(-1);
      if (lastChar === ".") {
        setExpression(expression.slice(0, -1) + operator); // Remove decimal point if followed by operator
      } else {
        setExpression(expression + operator);
      }
    }
  };
const calculate = () => {
  try {
    const expressionToEvaluate = expression.replace(/[*+-]-/g, '$& ');
    const calculationResult = math.evaluate(expressionToEvaluate);
    setResult(calculationResult.toString());
    setExpression(""); // Clear the expression display
  } catch (error) {
    console.error('Error calculating result:', error);
    setResult('Error');
  }
};
  return (
    <div id="calculator">
        <div className="content">
        <div id="display">
          <div className="inner-div">{expression}</div>
          <div className="inner-div">{result}</div>
        </div>
        <button id="clear" onClick={clear}>AC</button>
        <button id="divide" onClick={addOperator}>/</button>

        <button id="seven" onClick={numberInput}>7</button>
        <button id="eight" onClick={numberInput}>8</button>
        <button id="nine" onClick={numberInput}>9</button>
        <button id="multiply" onClick={addOperator}>*</button>

        <button id="four" onClick={numberInput}>4</button>
        <button id="five" onClick={numberInput}>5</button>
        <button id="six" onClick={numberInput}>6</button>
        <button id="subtract" onClick={addOperator}>-</button>

        <button id="one" onClick={numberInput}>1</button>
        <button id="two" onClick={numberInput}>2</button>
        <button id="three" onClick={numberInput}>3</button>
        <button id="add" onClick={addOperator}>+</button>
         
        <button id="zero" onClick={numberInput}>0</button>
        <button id="decimal" onClick={decimal}>.</button>
        <button id="equals" onClick={calculate}>=</button>
      </div>
    </div>
  )
}

export default Calc